.App {
  text-align: center;
}
.chart-container {
  margin: auto;
  height: 90vh;
  width: 90vw;
}
@media (orientation: landscape) {
  canvas {
    height: 80vh !important;
    width: auto !important;
    margin: auto;
  }
}
@media (orientation: portrait) {
  canvas {
    width: 80vw !important;
    height: auto !important;
    margin: auto;
  }
}
.pagination {
  display: flex;
  justify-content: center;
}
.flex {
  display: flex;
  width: 85vw;
  justify-content: space-around;
}
.popover {
  padding: 1vw;
}
